import './App.css';
import { useState } from 'react'
import axios from 'axios'

function App() {
  const [state, setState] = useState({ 
    isActive : false,
    url: '',
    link: ''
  })

  const handleChange = (e) => {
    if (!state.isActive) {
      setState(() => ({ ...state, url: e.target.value }))
    }
  }

  const makeShortLink = async () => {
    const re = new RegExp("^(?:(?:http(?:s)?|ftp)://)(?:\\S+(?::(?:\\S)*)?@)?(?:(?:[a-z0-9\u00a1-\uffff](?:-)*)*(?:[a-z0-9\u00a1-\uffff])+)(?:\\.(?:[a-z0-9\u00a1-\uffff](?:-)*)*(?:[a-z0-9\u00a1-\uffff])+)*(?:\\.(?:[a-z0-9\u00a1-\uffff]){2,})(?::(?:\\d){2,5})?(?:/(?:\\S)*)?$");
    if (re.test(state.url)) {
      const res = await axios.post('https://shortlinks-app.herokuapp.com/short-links', {
        url: state.url
      })
      if (res.data.data && !state.isActive) {
        setState(() => ({ 
          ...state, 
          isActive: true, 
          link: 'https://shortlinks-app.herokuapp.com/r/' + res.data.data.shortId,
          url: ''
        }))
      }
    } else {
      alert('Invalid URL!')
    }

  }

  const backHome = () => {
    setState(() => ({ 
      ...state, 
      isActive: false,
      link: ''
    }))
  }

  const copyToClipboard = () => {
    const el = document.getElementById('url')
    el.select()
    document.execCommand('copy')
  }

  return (
    <div>
        <div className="container py-4">
          <header className="pb-3 mb-4 border-bottom">
            <a href="/" className="d-flex align-items-center text-dark text-decoration-none">
              <span className="fs-3">Short Link App</span>
            </a>
          </header>

          <div className="p-5 mb-4 bg-light rounded-3">
            <div className="container-fluid py-5">
              <h1 className="display-6 fw-bold">
                Simple is better than complex,<br />
                Complex is better than complicated.
              </h1>
              {
                state.isActive
                ? <label for="url" className="form-label">Short Link</label>
                : <label for="url" className="form-label">Original Link</label>
              }
              <input type="text" className="form-control" id="url" value={ state.isActive ? state.link : state.url } onChange={ handleChange }/><br />
              {
                state.isActive 
                ? 
                <div>
                  <button className="btn btn-outline-secondary" type="button" onClick={ backHome }>&lt;&lt; URL</button>
                  <button className="btn btn-outline-primary" type="button" onClick={ copyToClipboard }>Copy Link</button>
                </div>
                : <button className="btn btn-primary" type="button" onClick={ makeShortLink }>Shorten</button>
              }

            </div>
          </div>

        </div>
    </div>
  );
}

export default App;
